import React from 'react';
import {
  Box,
  Typography,
  Grid,
  Paper,
  Card,
  CardContent,
  CircularProgress,
  Alert,
} from '@mui/material';
import {
  Person,
  Inventory,
  Assignment,
  Warning,
} from '@mui/icons-material';
import { useUsers, useProducts, useLoanStats } from '../hooks';

const DashboardPage: React.FC = () => {
  const { data: usersData, isLoading: usersLoading, error: usersError } = useUsers({ limit: 1 });
  const { data: productsData, isLoading: productsLoading } = useProducts({ limit: 1 });
  const { data: loanStats, isLoading: statsLoading } = useLoanStats();

  const isLoading = usersLoading || productsLoading || statsLoading;

  if (usersError) {
    return (
      <Box sx={{ p: 3 }}>
        <Alert severity="error">
            : {usersError.message}
        </Alert>
      </Box>
    );
  }

  return (
    <Box sx={{ p: 3 }}>
      <Typography variant="h4" component="h1" fontWeight="bold" gutterBottom>
         
      </Typography>
      
      <Grid container spacing={3}>
        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                <Person color="primary" sx={{ fontSize: 40, mr: 2 }} />
                <Box>
                  <Typography variant="h4" color="primary">
                    {isLoading ? <CircularProgress size={24} /> : usersData?.pagination?.total || 0}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                      
                  </Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                <Inventory color="success" sx={{ fontSize: 40, mr: 2 }} />
                <Box>
                  <Typography variant="h4" color="success.main">
                    {isLoading ? <CircularProgress size={24} /> : productsData?.pagination?.total || 0}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                     
                  </Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                <Assignment color="info" sx={{ fontSize: 40, mr: 2 }} />
                <Box>
                  <Typography variant="h4" color="info.main">
                    {statsLoading ? <CircularProgress size={24} /> : loanStats?.active || 0}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                     
                  </Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Box sx={{ display: 'flex', alignItems: 'center' }}>
                <Warning color="error" sx={{ fontSize: 40, mr: 2 }} />
                <Box>
                  <Typography variant="h4" color="error.main">
                    {statsLoading ? <CircularProgress size={24} /> : loanStats?.overdue || 0}
                  </Typography>
                  <Typography variant="body2" color="text.secondary">
                    
                  </Typography>
                </Box>
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      <Box sx={{ mt: 4 }}>
        <Paper sx={{ p: 3 }}>
          <Typography variant="h6" gutterBottom>
             
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ textAlign: 'center', py: 4 }}>
                 
          </Typography>
        </Paper>
      </Box>
    </Box>
  );
};

export default DashboardPage;